import React from 'react';
import TextField from 'react-md/lib/TextFields';
import Button from 'react-md/lib/Buttons/Button';
import { browserHistory } from 'react-router';

class MainLayout extends React.Component {

    constructor(props) {
        super(props);
        this.state = {name: ''}
    };

    onChange = (name) => {
        this.setState({name})
    };

    goToSayings = () => {
        browserHistory.push(`/sayings/${this.state.name}`)
    };

  render() {
      return (
      <div className="app">
          <div className="container">
            <h1>Sanfte Gre!</h1>

              Bewertet wird von 1-3 Sternen, 1 fr schwach, 3 fr stark.<br />
              Das x ist nur fr den Fall, dass du einen Spruch absolut unangemessen oder anstig findest.<br />

              Dein Fortschritt wird gespeichert - gib deinen Namen ein (Schreibweise beachten) <br/>
              und du setzt da fort, wo du aufgehrt hast.<br />

              Frhliches Bewerten!<br/>
              Vielen Dank fr deine Hilfe!<br />

              <TextField
                  id="floatingTitle"
                  label="zb. Bruce Banner"
                  placeholder="Namen eingeben"
                  className="md-cell md-cell--bottom"
                  onChange={this.onChange}
              />
              <Button type="submit" onClick={this.goToSayings} raised primary label="Proceed"/>
          </div>
      </div>
    );
  }
};

export default MainLayout;



